
import { useState, useEffect, useCallback } from 'react';

export type Subject = 'science' | 'math' | 'social-studies';
const SUBJECTS: Subject[] = ['science', 'math', 'social-studies'];

const UNLOCKED_KEY = 'study-unlock-status';

const validateCode = (code: string, subject: Subject): 'subject' | 'all' | false => {
    const upperCaseCode = code.toUpperCase();
    if (upperCaseCode === 'ALLACCESS') return 'all';
    if (subject === 'science' && upperCaseCode === 'SCIENCE123') return 'subject';
    if (subject === 'math' && upperCaseCode === 'MATH456') return 'subject';
    if (subject === 'social-studies' && upperCaseCode === 'SOCIAL789') return 'subject';
    return false;
};

export const useUnlockStore = () => {
    const [unlockedSubjects, setUnlockedSubjects] = useState<Subject[]>([]);
    const [isInitialized, setIsInitialized] = useState(false);

    useEffect(() => {
        // This code now runs only on the client, after the component has mounted.
        try {
            const storedStatus = window.localStorage.getItem(UNLOCKED_KEY);
            if (storedStatus) {
                const parsedStatus = JSON.parse(storedStatus);
                if (Array.isArray(parsedStatus)) {
                    setUnlockedSubjects(parsedStatus);
                }
            }
        } catch (error) {
            console.error('Failed to load unlock status from localStorage', error);
        }
        setIsInitialized(true);
    }, []);

    const saveToLocalStorage = (subjects: Subject[]) => {
        try {
            window.localStorage.setItem(UNLOCKED_KEY, JSON.stringify(subjects));
        } catch (error) {
            console.error('Failed to save unlock status to localStorage', error);
        }
    };

    const unlockSubject = useCallback((subject: Subject, code: string): boolean => {
        const validationResult = validateCode(code, subject);
        if (validationResult) {
            let newUnlockedSubjects: Subject[];
            if (validationResult === 'all') {
                newUnlockedSubjects = [...SUBJECTS];
            } else {
                newUnlockedSubjects = [...new Set([...unlockedSubjects, subject])];
            }
            setUnlockedSubjects(newUnlockedSubjects);
            saveToLocalStorage(newUnlockedSubjects);
            return true;
        }
        return false;
    }, [unlockedSubjects]);

    const isUnlocked = useCallback((subject: Subject): boolean => {
        return unlockedSubjects.includes(subject);
    }, [unlockedSubjects]);

    return { isUnlocked, unlockSubject, isInitialized };
};
